import { graphql, useStaticQuery } from "gatsby";

import { JobDesc, isValidJobDesc, Layout } from "components";

const Work = () => {
  const { workHistory } = useStaticQuery<Queries.WorkHistoryQuery>(query);
  return (
    <Layout>
      {workHistory.edges.map(
        ({ node }) =>
          isValidJobDesc(node) && <JobDesc key={node.project} {...node} />
      )}
    </Layout>
  );
};

export const query = graphql`
  query WorkHistory {
    workHistory: allWorkJson {
      edges {
        node {
          project
          longName
          intro
          contribution
          links {
            url
            str
          }
        }
      }
    }
  }
`;

export default Work;
